.p{
    width: 30%;
    height: 40vh;
    margin :20px 10px;
    border: 2px solid rgb(243,242,242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser{
    height: 20px;
    background-color:rgb(243,242,242) ;
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.p-circle{
    width: 6px;
    height: 6px;
    background-color: white;
    border-radius: 50%;
    margin: 2px;
}

.p-img
{
    width: 100%;
    transition: all 2s ease;
}

.p:hover .p-img{
    transform: translateY(-30%)
}

@media screen and (max-width:480px)
{
    .p{
        width: 40%;
        height: 15vh;
        font-size:10px;
    }
}